import { DataColumn } from '@farris/ui-datagrid';
import { Component, OnInit, Input, Injector } from '@angular/core';

@Component({
    selector: 'filter-editor',
    template: '',
})
export class FilterRowEditorComponent implements OnInit {

    @Input() column: DataColumn;
    id = '';
    constructor() { }

    ngOnInit(): void {
        this.id = `filter-row_${this.column.field}`;
    }

    clearValue() {}
}
