import { Subscription } from 'rxjs';
import { DatagridFilterRowService } from './datagrid-filter-row.service';
import { Component, OnInit, Input, AfterViewInit, ElementRef, Renderer2,
    NgZone, Output, EventEmitter, OnDestroy, Injector, ViewEncapsulation, ViewChildren, QueryList, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { DataColumn, DatagridComponent } from '@farris/ui-datagrid';
import { debounce, debounceTime, distinctUntilChanged, map } from 'rxjs/operators';
import { FilterCellEditorDirective } from './datagrid-filter-cell-editor.directive';

@Component({
    selector: '[filter-row]',
    templateUrl: './datagrid-filter-row.component.html',
    styleUrls: [
        './datagrid-filter-row.scss'
    ],
    providers: [
        DatagridFilterRowService
    ],
    encapsulation: ViewEncapsulation.None
})
export class FilterRowComponent implements OnInit, AfterViewInit, OnDestroy {
    @Input() fixedLeft = false;
    @Input() fixedRight = false;
    @Input() columns: DataColumn[];
    /** datagrid Instance */
    @Input() gridInstance: DatagridComponent;

    @Input() delay = 300;

    @Output() conditionChange = new EventEmitter();

    @ViewChildren(FilterCellEditorDirective) editors: QueryList<FilterCellEditorDirective>;

    filterRowConditionsScribe: Subscription;
    form = new FormGroup({});
    tr: HTMLTableRowElement;
    constructor(
        private fb: FormBuilder,
        private el: ElementRef,
        private ngzone: NgZone,
        private cd: ChangeDetectorRef,
        public dfrs: DatagridFilterRowService,
        private render: Renderer2,
        private inject: Injector) {
            this.filterRowConditionsScribe = this.dfrs.filterRowConditions$.subscribe(ccs => {
                this.conditionChange.emit(ccs);
            });
        }

    ngOnInit(): void {
        this.form = this.createControl();
        this.ngzone.runOutsideAngular(() => {
            this.render.listen('document', 'click', () => {
                this.dfrs.closeFilterPanel();
            });
        });
    }

    ngAfterViewInit() {
        const nativeEl = this.el.nativeElement;
        this.tr = nativeEl.parentElement;
        nativeEl.querySelectorAll('td').forEach(n => {
            this.tr.append(n);
        });
        nativeEl.style.display = 'none';

        this.dfrs.gridInstance = this.gridInstance;
    }

    ngOnDestroy() {
        if (this.filterRowConditionsScribe) {
            this.filterRowConditionsScribe.unsubscribe();
            this.filterRowConditionsScribe = null;
        }
        if (this.tr) {
            const tds = this.tr.querySelectorAll('td');
            if (tds && tds.length) {
                tds.forEach(a => {
                    if (!a.classList.contains('f-datagrid-header-checkbox') && !a.classList.contains('f-datagrid-cell-rownumber')) {
                        this.tr.removeChild(a);
                    }
                });
            }
        }
    }

    createControl() {
        const group = this.fb.group({});
        this.columns.forEach(col => {
            if (col.colspan) {return; }
            const control = this.fb.control('');
            group.addControl(col.field, control);
        });
        return group;
    }

    clearFilterValue(emitEvent = true) {
        // this.form.reset();
        this.editors.forEach(c => {
            c.clearValue();
        });

        this.dfrs.clear(emitEvent);
    }

    removeField(field: string, opts?: { emitEvent: boolean }) {
        this.dfrs.removeFilterField(field, opts);
    }
}
