export enum FilterOperator {
    Equal = 0,
    NotEqual = 1,
    Greater = 2,
    GreaterOrEqual = 3,
    Less = 4,
    LessOrEqual = 5,
    Contains = 6,
    NotContains = 9,
    In = 14,
    NotIn = 15,
    Empty = 1001,
    NotEmpty = 1002,
    Null = 1003,
    NotNull = 1004
}

export enum FilterConditionValue {
    All = '',
    Empty = 'null'
}

export interface FilterEnumSetting {
    valueField?: string;
    textField?: string;
    data?: any[];
    idField?: string;
}

export interface FilterRowOperator {
    code: FilterOperator;
    label: string;
    isDefault: boolean;
}

export interface FilterCondition {
    operator1?: FilterOperator;
    value1?: any;
    relation?: 'and' | 'or' | '';
    operator2?: FilterOperator;
    value2?: any;
}

export type ColumnConditionType = FilterCondition | FilterConditionValue | string;

export interface ColumnFilterCondition {
    [fieldName: string]: ColumnConditionType;
}


export interface FilterData {
    filterField: string;  value: string; lbracket?: string; rbracket?: string;
    relation?: any; compare?: any;
}

